export default {
  methods: {
    show() {
      this.$modal.show('test-nights')
    },
    close() {
      this.$modal.hide('test-nights')
    }
  },
  mounted() {
    const _self = this
    let show = true

    if(this.$isMobile()) {
      setTimeout(() => {
        this.show()
      }, 16000)
    }

    else {
      document.body.addEventListener('mouseout', function(e) {
        if (!e.relatedTarget && !e.toElement && show) {
          show = false
          _self.show()
        }
      })
    }
  },
}
